/*-------------General Style---------------------------------------*/
html{
	overflow-x:hidden !important;
	overflow-y:scroll !important;
	height:100%;
}
body{
	margin: 0;
	padding: 0;
	font-weight: 400;
	font-size: 12px;
	height:100%;
	text-align:center;
	background:#454545;
	color:#000;
	font-family: 'Raleway', sans-serif;
}

/*--------------Typography--------------------------------------*/
p {
    text-align: left;
    font-size: 12px;
    line-height: 24px;
    padding-bottom: 10px;
	font-weight:500;
}
blockquote {
	float:left;
	padding: 10px 20px;
	margin: 0 0 20px;
	font-size: 17.5px;
	border-left: 15px solid #eee;
}
blockquote p {
	font-family: Georgia, "Times New Roman", Times, serif;
	font-style:italic;
	color:#494949;
}
.text-subtitle {
	float:left;
	width:100%;
	position:relative;
	font-size:14px;
	font-weight:bold;
	letter-spacing:2;
	font-family: 'Montserrat', sans-serif;
	text-transform:uppercase;
	color:#666;
	padding-bottom:10px;
	text-align:left;
}
.text-title {
	float:left;
	width:100%;
	position:relative;
	font-size:26px;
	font-weight:bold;
	letter-spacing:2px;
	color:#191919;
	text-transform:uppercase;
	text-align:left;
	padding-bottom:10px;
	border-bottom:1px solid #eee;
	margin-bottom:10px;
}
/*------ loader-------------------------------------------------*/
.loader-holder {
	 position:fixed;
	 width:50px;
	 height:50px;
	 top:50%;
	 left:50%;
	 margin-top:-20px;
	 margin-left:-20px;
	 z-index:150;
}
.loader-inner {
	top:0;
	left:0;
	position:absolute;
  	width: 50px;
  	height: 50px;
  	z-index:20;
	transition: all 0.4s  ease-in-out;
}
.loader {
	float:left;
	width: 50px;
	height: 50px;
	border-left:10px solid #404040;
	-webkit-animation: load8 1.1s infinite linear;
	animation: load8 1.1s infinite linear;
}
.loader , .loader:after {
	border-radius: 50%;
	width: 40px;
	height: 40px;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
/* ---------Content Styles--------------------------------------*/
#main{
	height:100%;
	width:100%;
	position:absolute;
	top:0;
	left:0;
	z-index:2;
	opacity:0;
}
.container{
	max-width: 1024px;
	width:92%;
	margin:0 auto;
	position: relative;
	z-index:5;
}
#wrapper {
	float:left;
	width:100%;
	position:relative;
	background:#fff;
}
.content {
	width:100%;
	position:relative;
	z-index:4;
	float:left;
	padding:0 30px;
	background:#fff;
}
.content.fscon {
	padding:0 70px;
}
.column-wrap {
	float:right;
	width:70%;
	position:relative;
	padding-right:70px;
}
.column-wrap #wrapper {
	padding:0 30px 60px 30px;
}
.fl-wrap {
	float:left;
	width:100%;
	position:relative;
}
.no-bg {
	background:none;
}
.full-height {
	height:100%;
}
.full-width-wrap {
	width:100%;
}
.bg {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	background-size: cover;
	background-attachment: scroll  ;
	background-position: center  ;
	background-repeat:repeat;
	background-origin: content-box;
}
.bg.par-elem {
	height:150%;
	top:-15%;
}
.full-height-parallax {
	position: absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	overflow:hidden;
	z-index:2;
}
.respimg {
	width:100%;
	height:auto;
}
.overlay {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	background:#000;
	opacity:0.2;
}
.hero-wrap {
	position:relative;
	width:100%;
	overflow:hidden;
	height:100%;
	z-index:10;
}
.parallax-title {
	position:absolute;
	top:10%;
	z-index:1;
	font-size:94px;
	color:#000;
	opacity:0.07;
	text-transform:uppercase;
	font-weight:900;
}
.parallax-title.left-pos {
	left:0;
}
.parallax-title.right-pos {
	right:50px;
}
.no-padding {
	padding:0 !important;
}
.dec-list {
	float:left;
	width:100%;
}
.dec-list li {
	float:left;
	width:100%;
	text-align:left;
	position:relative;
	padding:15px 0 15px 40px;
	font-size:12px;
	border-bottom:1px solid #eee;
	font-weight:600;
}
.dec-list li span {
	color:#666;
}
.dec-list li:before {
    font-family: FontAwesome;
    font-style: normal;
    font-weight: normal;
    text-decoration: inherit;
	content: "\f178";
	position:absolute;
	top:15px;
	font-size:16px;
	left:0;
}
.cts-dot {
	display:none;
}
/*------ Fixed-column ------------------------------------------------*/
.fixed-column {
	position:fixed;
	top:0;
	left:0;
	width:30%;
	height:100%;
	padding-left:70px;
	z-index:4;
	-webkit-transform: translate3d(1px,0,0);
	overflow:hidden;
}
.progress-bar-wrap {
	position:absolute;
	top:20px;
	left:100px;
	right:30px;
	height:20px;
	z-index:20;
}
.progress-bar-wrap:before {
	content:'';
	position:absolute;
	top:50%;
	left:0;
	width:100%;
	height:1px;
	background:rgba(255,255,255,0.51);
	z-index:1;
