	content:'';
	position:absolute;
	top:10px;
	left:-5px;
	width:30px;
	height:1px;
	z-index:5;
}
.show-share.isShare:before {
	width:0;
}
.show-share:after {
	content:"share";
	position:absolute;
	top:-36px;
	left:-2px;
	font-family: Georgia, "Times New Roman", Times, serif;
	float:left;
	text-align:center;
	font-style:italic;
	color:#fff;
	opacity:0;
	visibility:hidden;
}
